Vue.prototype.getTags = function(hasall){
  return new Promise(function (resolve, reject){
    Vue.prototype.requests({reqUrl:'/api/index/classify_tags'}).then(function(res){
      var cndata = [],endata=[];
      if(hasall){
        cndata[0] ='全部';
        endata[0] = 'All';
      }
      
      for(var i=0;i<res.data.length; i++){
          cndata[hasall?i+1:i] = res.data[i].tags_name;
          endata[hasall?i+1:i] = res.data[i].tags;
      }
      resolve({'cndata':cndata,'endata':endata});
    })
  })
}

// 关闭加载弹框--用于请求数据的关闭
Vue.prototype.closeLoading = function(handles){
  if(handles){
    if(handles.closeLoading){
      if(handles.closeLoadTime){
        setTimeout(function(){
          MintUI.Indicator.close();
        },handles.closeLoadTime)
      }else{
        MintUI.Indicator.close();
      }
    }
  }else{
    // Indicator.close();
    setTimeout(function(){
      MintUI.Indicator.close();
    },600)
  }
}

// 显示加载弹框
Vue.prototype.showLoading = function(text){
  MintUI.Indicator.open({
    text: text?text:'加载中...',
    spinnerType: 'fading-circle'
  });
}
// 隐藏加载弹框
Vue.prototype.hideLoading = function(times){
  if(times){
    setTimeout(function(){
      MintUI.Indicator.close();
    },times)
  }else{
    MintUI.Indicator.close();
  }
}
// 显示消息弹框
Vue.prototype.showMsg = function({text:text,weizhi:weizhi,times:times}){
  if(Vue.prototype.isApp){
    api.toast({
      msg: text,
      duration: times?times:2000,
      location: weizhi?weizhi:'middle'
    });
  }else{
    MintUI.Toast({
      message: text,
      position: weizhi?weizhi:'middle',
      duration: times?times:2000
    })
  }
}

// 判断是否有网络
Vue.prototype.checkLine = function(){
  // unknown          未知
  // ethernet         以太网
  // wifi             wifi
  // 2g               2G网络
  // 3g               3G网络
  // 4g               4G网络
  // none             无网络
  return Vue.prototype.isApp?api.connectionType:'';
}

